import React, { useState, useEffect } from 'react';
import { useFormik } from 'formik';
import * as yup from 'yup';
import {
  Box,
  Container,
  Grid,
  Paper,
  Typography,
  TextField,
  Button,
  Tabs,
  Tab,
  Alert,
  CircularProgress,
} from '@mui/material';
import {
  Person as PersonIcon,
  Lock as LockIcon,
  School as SchoolIcon,
  Edit as EditIcon,
  MenuBook as MenuBookIcon,
  CheckCircle as CheckCircleIcon,
} from '@mui/icons-material';
import { useAuth } from '../contexts/AuthContext';
import LoadingSpinner from '../components/LoadingSpinner';
import UserCourseCard from '../components/UserCourseCard';
import userCoursesService, { UserCourse } from '../services/userCourses.service';

interface TabPanelProps {
  children?: React.ReactNode;
  index: number;
  value: number;
}

const TabPanel = (props: TabPanelProps) => {
  const { children, value, index, ...other } = props;

  return (
    <div
      role="tabpanel"
      hidden={value !== index}
      id={`profile-tabpanel-${index}`}
      aria-labelledby={`profile-tab-${index}`}
      {...other}
    >
      {value === index && <Box sx={{ py: 3 }}>{children}</Box>}
    </div>
  );
};

const Profile: React.FC = () => {
  const { user, updateProfile, changePassword } = useAuth();
  const [tabValue, setTabValue] = useState(0);
  const [loading, setLoading] = useState(false);
  
  // États pour les cours de l'utilisateur
  const [enrolledCourses, setEnrolledCourses] = useState<UserCourse[]>([]);
  const [completedCourses, setCompletedCourses] = useState<UserCourse[]>([]);
  const [inProgressCourses, setInProgressCourses] = useState<UserCourse[]>([]);
  const [coursesLoading, setCoursesLoading] = useState(false);
  const [coursesError, setCoursesError] = useState<string | null>(null);

  const handleTabChange = (event: React.SyntheticEvent, newValue: number) => {
    setTabValue(newValue);
  };

  // Fonction pour charger les cours de l'utilisateur
  const loadUserCourses = async () => {
    if (tabValue === 2) { // Onglet "Mes Cours"
      console.log('🔄 Chargement des cours utilisateur...');
      setCoursesLoading(true);
      setCoursesError(null);
      
      try {
        // Utiliser une seule requête pour récupérer tous les cours
        console.log('📡 Appel de userCoursesService.getEnrolledCourses()...');
        const allCourses = await userCoursesService.getEnrolledCourses();
        console.log('✅ Cours récupérés:', allCourses);
        
        // Filtrer les cours côté client
        const completed = allCourses.filter(course => course.completed);
        const inProgress = allCourses.filter(course => !course.completed && course.progress > 0);
        
        console.log('📊 Filtrage des cours:');
        console.log('   - Total:', allCourses.length);
        console.log('   - Terminés:', completed.length);
        console.log('   - En cours:', inProgress.length);
        
        setEnrolledCourses(allCourses);
        setCompletedCourses(completed);
        setInProgressCourses(inProgress);
      } catch (error: any) {
        console.error('❌ Erreur lors du chargement des cours:', error);
        console.error('   Status:', error.response?.status);
        console.error('   Message:', error.response?.data?.message);
        setCoursesError(error.response?.data?.message || 'Erreur lors du chargement des cours');
      } finally {
        setCoursesLoading(false);
      }
    }
  };

  // Charger les cours quand l'onglet "Mes Cours" est sélectionné
  useEffect(() => {
    if (tabValue === 2) {
      // Ajouter un délai pour éviter les requêtes multiples
      const timer = setTimeout(() => {
        loadUserCourses();
      }, 100);
      
      return () => clearTimeout(timer);
    }
  }, [tabValue]);

  const handleContinueCourse = (courseId: string) => {
    // Navigation vers la page d'apprentissage du cours
    window.location.href = `/courses/${courseId}/learn`;
  };

  const profileFormik = useFormik({
    initialValues: {
      firstName: user?.firstName || '',
      lastName: user?.lastName || '',
      email: user?.email || '',
    },
    validationSchema: yup.object({
      firstName: yup.string().required('Le prénom est requis'),
      lastName: yup.string().required('Le nom est requis'),
      email: yup.string().email('Email invalide').required('L\'email est requis'),
    }),
    onSubmit: async (values) => {
      setLoading(true);
      try {
        await updateProfile(values);
        // Afficher un message de succès
      } catch (error) {
        console.error('Erreur lors de la mise à jour du profil:', error);
      } finally {
        setLoading(false);
      }
    },
  });

  const passwordFormik = useFormik({
    initialValues: {
      currentPassword: '',
      newPassword: '',
      confirmPassword: '',
    },
    validationSchema: yup.object({
      currentPassword: yup.string().required('Le mot de passe actuel est requis'),
      newPassword: yup
        .string()
        .min(8, 'Le mot de passe doit contenir au moins 8 caractères')
        .matches(
          /^(?=.*[a-z])(?=.*[A-Z])(?=.*\d)(?=.*[@$!%*?&])[A-Za-z\d@$!%*?&]{8,}$/,
          'Le mot de passe doit contenir au moins une majuscule, une minuscule, un chiffre et un caractère spécial'
        )
        .required('Le nouveau mot de passe est requis'),
      confirmPassword: yup
        .string()
        .oneOf([yup.ref('newPassword')], 'Les mots de passe ne correspondent pas')
        .required('La confirmation du mot de passe est requise'),
    }),
    onSubmit: async (values) => {
      setLoading(true);
      try {
        await changePassword(values.currentPassword, values.newPassword);
        passwordFormik.resetForm();
        // Afficher un message de succès
      } catch (error) {
        console.error('Erreur lors du changement de mot de passe:', error);
      } finally {
        setLoading(false);
      }
    },
  });

  if (loading) {
    return <LoadingSpinner />;
  }

  return (
    <Container maxWidth="lg">
      <Box sx={{ mb: 4 }}>
        <Typography variant="h4" component="h1" gutterBottom>
          Mon Profil
        </Typography>
        <Typography variant="body1" color="text.secondary">
          Gérez vos informations personnelles et vos préférences
        </Typography>
      </Box>

      <Paper sx={{ width: '100%', mb: 4 }}>
        <Tabs
          value={tabValue}
          onChange={handleTabChange}
          aria-label="profile tabs"
          sx={{ borderBottom: 1, borderColor: 'divider' }}
        >
          <Tab icon={<PersonIcon />} label="Informations personnelles" />
          <Tab icon={<LockIcon />} label="Sécurité" />
          <Tab icon={<SchoolIcon />} label="Mes cours" />
        </Tabs>

        <TabPanel value={tabValue} index={0}>
          <Box component="form" onSubmit={profileFormik.handleSubmit}>
            <Grid container spacing={3}>
              <Grid item xs={12} sm={6}>
                <TextField
                  fullWidth
                  id="firstName"
                  name="firstName"
                  label="Prénom"
                  value={profileFormik.values.firstName}
                  onChange={profileFormik.handleChange}
                  error={profileFormik.touched.firstName && Boolean(profileFormik.errors.firstName)}
                  helperText={profileFormik.touched.firstName && profileFormik.errors.firstName}
                />
              </Grid>
              <Grid item xs={12} sm={6}>
                <TextField
                  fullWidth
                  id="lastName"
                  name="lastName"
                  label="Nom"
                  value={profileFormik.values.lastName}
                  onChange={profileFormik.handleChange}
                  error={profileFormik.touched.lastName && Boolean(profileFormik.errors.lastName)}
                  helperText={profileFormik.touched.lastName && profileFormik.errors.lastName}
                />
              </Grid>
              <Grid item xs={12}>
                <TextField
                  fullWidth
                  id="email"
                  name="email"
                  label="Email"
                  value={profileFormik.values.email}
                  onChange={profileFormik.handleChange}
                  error={profileFormik.touched.email && Boolean(profileFormik.errors.email)}
                  helperText={profileFormik.touched.email && profileFormik.errors.email}
                />
              </Grid>
              <Grid item xs={12}>
                <Button
                  type="submit"
                  variant="contained"
                  startIcon={<EditIcon />}
                >
                  Mettre à jour le profil
                </Button>
              </Grid>
            </Grid>
          </Box>
        </TabPanel>

        <TabPanel value={tabValue} index={1}>
          <Box component="form" onSubmit={passwordFormik.handleSubmit}>
            <Grid container spacing={3}>
              <Grid item xs={12}>
                <TextField
                  fullWidth
                  id="currentPassword"
                  name="currentPassword"
                  label="Mot de passe actuel"
                  type="password"
                  value={passwordFormik.values.currentPassword}
                  onChange={passwordFormik.handleChange}
                  error={passwordFormik.touched.currentPassword && Boolean(passwordFormik.errors.currentPassword)}
                  helperText={passwordFormik.touched.currentPassword && passwordFormik.errors.currentPassword}
                />
              </Grid>
              <Grid item xs={12}>
                <TextField
                  fullWidth
                  id="newPassword"
                  name="newPassword"
                  label="Nouveau mot de passe"
                  type="password"
                  value={passwordFormik.values.newPassword}
                  onChange={passwordFormik.handleChange}
                  error={passwordFormik.touched.newPassword && Boolean(passwordFormik.errors.newPassword)}
                  helperText={passwordFormik.touched.newPassword && passwordFormik.errors.newPassword}
                />
              </Grid>
              <Grid item xs={12}>
                <TextField
                  fullWidth
                  id="confirmPassword"
                  name="confirmPassword"
                  label="Confirmer le nouveau mot de passe"
                  type="password"
                  value={passwordFormik.values.confirmPassword}
                  onChange={passwordFormik.handleChange}
                  error={passwordFormik.touched.confirmPassword && Boolean(passwordFormik.errors.confirmPassword)}
                  helperText={passwordFormik.touched.confirmPassword && passwordFormik.errors.confirmPassword}
                />
              </Grid>
              <Grid item xs={12}>
                <Button
                  type="submit"
                  variant="contained"
                  startIcon={<LockIcon />}
                >
                  Changer le mot de passe
                </Button>
              </Grid>
            </Grid>
          </Box>
        </TabPanel>

        <TabPanel value={tabValue} index={2}>
          {coursesLoading ? (
            <Box sx={{ display: 'flex', justifyContent: 'center', py: 4 }}>
              <CircularProgress />
            </Box>
                     ) : coursesError ? (
             <Alert 
               severity="error" 
               sx={{ mb: 2 }}
               action={
                 <Button 
                   color="inherit" 
                   size="small" 
                   onClick={loadUserCourses}
                 >
                   Réessayer
                 </Button>
               }
             >
               {coursesError}
             </Alert>
          ) : (
            <Grid container spacing={3}>
              {/* Cours en cours */}
              <Grid item xs={12}>
                <Box sx={{ display: 'flex', alignItems: 'center', mb: 2 }}>
                  <MenuBookIcon sx={{ mr: 1, color: 'primary.main' }} />
                  <Typography variant="h6" gutterBottom sx={{ mb: 0 }}>
                    Cours en cours ({inProgressCourses.length})
                  </Typography>
                </Box>
                {inProgressCourses.length > 0 ? (
                  inProgressCourses.map((userCourse) => (
                    <UserCourseCard
                      key={userCourse._id}
                      userCourse={userCourse}
                      onContinue={handleContinueCourse}
                    />
                  ))
                ) : (
                  <Alert severity="info">
                    Vous n'avez pas encore commencé de cours.
                  </Alert>
                )}
              </Grid>

              {/* Cours terminés */}
              <Grid item xs={12}>
                <Box sx={{ display: 'flex', alignItems: 'center', mb: 2, mt: 4 }}>
                  <CheckCircleIcon sx={{ mr: 1, color: 'success.main' }} />
                  <Typography variant="h6" gutterBottom sx={{ mb: 0 }}>
                    Cours terminés ({completedCourses.length})
                  </Typography>
                </Box>
                {completedCourses.length > 0 ? (
                  completedCourses.map((userCourse) => (
                    <UserCourseCard
                      key={userCourse._id}
                      userCourse={userCourse}
                      onContinue={handleContinueCourse}
                    />
                  ))
                ) : (
                  <Alert severity="info">
                    Vous n'avez pas encore terminé de cours.
                  </Alert>
                )}
              </Grid>

              {/* Tous les cours inscrits */}
              <Grid item xs={12}>
                <Box sx={{ display: 'flex', alignItems: 'center', mb: 2, mt: 4 }}>
                  <SchoolIcon sx={{ mr: 1, color: 'secondary.main' }} />
                  <Typography variant="h6" gutterBottom sx={{ mb: 0 }}>
                    Tous mes cours ({enrolledCourses.length})
                  </Typography>
                </Box>
                {enrolledCourses.length > 0 ? (
                  enrolledCourses.map((userCourse) => (
                    <UserCourseCard
                      key={userCourse._id}
                      userCourse={userCourse}
                      onContinue={handleContinueCourse}
                    />
                  ))
                ) : (
                  <Alert severity="info">
                    Vous n'êtes inscrit à aucun cours pour le moment.
                  </Alert>
                )}
              </Grid>
            </Grid>
          )}
        </TabPanel>
      </Paper>
    </Container>
  );
};

export default Profile; 